import { Types } from '../constants/actionTypes';

const initialState = {
  idToken: '',
  refreshToken: '',
  isAuthenticated: false,
  isTokenVerified: false,
  isAdmin: false,
}

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case Types.SIGNIN:
      return {
        ...state,
      }
    case Types.SIGNIN_SUCCESS:
      return {
        ...state,
      }
    case Types.SIGNIN_FAILURE:
      return {
        ...state,
      }
    case Types.LOGIN_SUCCESS:
      return {
        ...state,
        isAuthenticated: true,
      }
    case Types.LOGIN_FAILURE:
      return {
        ...state,
        idToken: '',
        refreshToken: '',
        isAuthenticated: false,
        isTokenVerified: false,
        isAdmin: false,
      }
    case Types.LOGOUT:
      return {
        ...state,
        idToken: '',
        refreshToken: '',
        isAuthenticated: false,
        isTokenVerified: false,
        isAdmin: false,
      }
    case Types.SESSION_UPDATE_SUCCESS:
      return {
        ...state,
        idToken: action.data.idToken,
        refreshToken: action.data.refreshToken,
        isAuthenticated: true,
        isTokenVerified: true,
      }
    case Types.SESSION_UPDATE_FAILURE:
      return {
        ...state,
        idToken: '',
        refreshToken: '',
        isAuthenticated: false,
        isTokenVerified: false,
        isAdmin: false,
      }
    case Types.GET_ME:
      return {
        ...state,
        isAdmin: action.isAdmin
      }
    default:
      return state;
  }
}

export default reducer;