import { take, put, call, fork, select, takeEvery } from 'redux-saga/effects'
import axios from 'axios'
import { ActionAuth } from '../actions/auth'
import { Types } from '../constants/actionTypes';

function signInAPI(payload) {
    return axios.post(
        'https://kgenots-api-server.herokuapp.com/graphql',
        {
            query: `
                mutation {
                    signIn(email:"${payload.email}", password:"${payload.password}")
                }
            `
        }
    )
}

function* fetchSignIn(action) {
    try {
        console.log('test', action)
        const data = yield call(signInAPI, action.payload)
        console.log(data);
        if (!data) data = 'null'
        yield put(ActionAuth.signInSuccess(data));
    } catch (error) {
        console.log(error);
        yield put(ActionAuth.signInFailure(error));
    }
}

export default [
    takeEvery(Types.SIGNIN, fetchSignIn)
]