const initialState = {
    sidebarShow: 'responsive',
    toasts: []
}

const reducer = (state = initialState, { type, ...rest }) => {
    switch (type) {
        case 'set':
            return { ...state, ...rest }
        case 'add_toast':
            return { 
                ...state,
                toasts: state.toasts.concat(rest.toast)
            }
        default:
            return state    
    }
}

export default reducer